<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="billNumber" class="fl">
          <el-select
            v-model="query.billNumber"
            filterable
            clearable
            placeholder="调拨单号"
            style="width: 178px;"
            class="filter-item"
          >
            <el-option
              v-for="(item, index) in billTransferPullList"
              :key="index"
              :label="item.billNumber"
              :value="item.billNumber"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <!-- <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button> -->
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="280"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @refresh="searchInit"
        @rowClick="handleRowClick"
      >
        <!-- Tip -->
        <!--
        未提交 展示提交 修改 删除 详情
        已提交 展示审核通过 审核不通过 驳回 撤回 详情
        驳回 展示提交 修改 删除 详情
        审核不通过，审核通过 展示详情
        1-未提交 2-审核中 3-未通过 4-已通过 5-驳回
        -->
        <template slot-scope="scope">
          <template v-if="[1, 5].includes(scope.row.status)">
            <el-button type="primary" @click="handle(scope.row, 'Submit')">提交</el-button>
            <el-button type="warning" @click="handleModify(scope.row)">修改</el-button>
            <el-button type="danger" @click="handle(scope.row, 'Delete')">删除</el-button>
          </template>
          <template v-if="scope.row.status === 2">
            <el-button type="warning" @click="handle(scope.row, 'Withdraw')">撤回</el-button>
          </template>
          <el-button type="primary" @click="handleDetail(scope.row)">详情</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import paginationPage from '@/mixins/paginationPage'
import { getListApi, deleteListApi, getBillTransferPullListApi, submitApi, withdrawApi } from '@/apis/modules/warehouse/discharge'
// import { requestWrapper, promiseWrapper } from '@/utils/utils'
import tableHeadSet from '@/mixins/tableHeadSet'
import { returnOfGoodsMap } from '@/views/in-storage/inspection/status'

const statusMap = {
  1: 'danger',
  2: 'warning',
  3: 'info',
  4: 'success'
}

export default {
  name: 'WarehouseDischarge',
  components: {
  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        billNumber: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'billNumber',
          label: '调拨单号',
          align: 'center',
          // width: 120,
          minWidth: '150',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'outWarehouseName',
          label: '移出仓库',
          align: 'center',
          minWidth: '100',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inWarehouseName',
          label: '移入仓库',
          align: 'center',
          minWidth: '100',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'submitterName',
          label: '提交人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'auditorName',
          label: '审核人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'date',
          label: '日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return returnOfGoodsMap[status - 1]
          },
          statusFormatter: (row) => {
            return statusMap[row.status]
          }
        }
      ],
      columnSets: [{
        module: 'status',
        prop: 'status',
        formatter: (status) => {
          return returnOfGoodsMap[status - 1]
        },
        statusFormatter: (row) => {
          return statusMap[row.status]
        }
      }],
      returnOfGoodsMap: returnOfGoodsMap,
      dialogForm: {},
      supplierPullList: [],
      // 关于查询项下拉
      pullListConfig: [
        { api: getBillTransferPullListApi(), dataKey: 'billTransferPullList', name: '物料组' }
      ],
      billTransferPullList: []
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.getPullList()
    this.init()
  },
  activated() {
    this.getPullList()
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        page: {
          ...this.pageInfo
        },
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 页面下拉数据
    getPullList() {
      const apis = this.pullListConfig.map(c => c.api)
      Promise.all(apis)
        .then(result => {
          if (result.length) {
            result.forEach((c, i) => {
              const dataKey = this.pullListConfig[i].dataKey
              this[dataKey] = !c.code ? c.data : []
            })
          }
        })
        .catch(reason => {
          console.log(reason)
        })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 操作按钮回调 提交、删除、撤回
    handle(item, name) {
      const obj = {
        Submit: { api: submitApi, tip: '该工单即将提交', message: '提交成功' },
        Delete: { api: deleteListApi, tip: '该工单即将删除', message: '删除成功' },
        Withdraw: { api: withdrawApi, tip: '该工单即将撤回', message: '撤回成功' }
      }
      this.$confirm(`${obj[name]['tip']}，是否继续？`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        obj[name]['api']({
          ...name === 'Delete' && { id: item.id },
          ...['Submit', 'Withdraw'].includes(name) && { billNumber: item.billNumber }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: `${obj[name]['message']}`
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '操作已取消'
        })
      })
    },
    handleAdd() {
      this.$router.push({
        name: 'BillTransferAdd'
      })
    },
    handleModify(item) {
      this.$router.push({
        name: 'BillTransferEdit',
        query: {
          billNumber: item.billNumber
        }
      })
    },
    handleDetail(item) {
      this.$router.push({
        name: 'BillTransferDetail',
        query: {
          billNumber: item.billNumber
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
